class htmlDecode  {
    htmlDecode(input){
        var e = document.createElement('textarea');
        e.innerHTML = input;
        // handle case of empty input
        return e.childNodes.length === 0 ? "" : e.childNodes[0].nodeValue;
      }
      convertQueryString(input){
        return input.replace(/%20/g, " ")
      }
    }
    export default htmlDecode;